ul {
    display:flex;
    flex-direction: row;
    justify-content: flex-start;
    align-content: flex-start;
    flex-wrap: wrap;
}
li{
    padding:10px;
    width:250;
    max-width:250;
    max-height:400;
}

.card{
    padding: 1rem;
    width: 100%;
    height: 90%;
    box-shadow:5px 5px 10px #ccc;
    align-items:center;
    display:flex;
    flex-direction: column;
    justify-content: center;
}

.card > img{

}

.card h3{
    margin:0px;
    padding: 5px;
}
 
.card article{
    padding:5px;
}

.card h3{
    padding: 3px;
}

